<template>
    <div class="flex-page goodsSearch" ref="flexPage">
        <van-nav-bar class="flex-full"  v-show="!isShowFixedSearch">
            <div slot="left" class="leftBar">
                <div class="backIcon" @click="goBack(isShowHistory)">
                    <img src="@/assets/images/common/nav_return_black.png" alt="">
                </div>
                <div class="searchInput searchDivBox"  @click="showFixedSearch">{{searchKey.length > 0 ? searchKey : placeholderWord}}</div>
                <img class="voiceIcon" src="@/assets/images/mall/nav_searchbar_icon_closed@2x.png" alt="" @click="voiceIconBtn">
            </div>
            <div slot="right" :class="themeCurrent=='1'?'rightBar-l':'rightBar'" @click="goToShoppingCart()">
                <img src="@/assets/images/newSlicing/icon_shop_cut.png" alt="">
                <div class="goodsNum" :class="skuTypeNum>=100?'goodsNumMax':''" v-show="skuTypeNum!=0">
                    {{skuTypeNum<=99?skuTypeNum:'99+'}}
                </div>
            </div>
        </van-nav-bar>
        <div class="post-wrapper" ref="postWrapper">
            <van-tabs
                    v-model="tabIndex"
                    @change="tabChange"
                    swipeable
                    :border="false"
                    line-height="3"
                    line-width="25"
                    color="#FB5768"
            >
                <slot slot="nav-left">
                    <div style=" width: 60px;">
                    </div>
                </slot>
                <slot slot="nav-right">
                    <div style="width: 60px;">
                    </div>
                </slot>
                <van-tab title="商品" name="0">
                    <div class="goods-content" :style="{height: contentListH +'px'}" ref="goodContent">
                        <div class="titleSortBtn" v-show="!isNetworkError">
                            <div class="searchTitleTxt" v-show="searchRult == 1">为您找到以下商品</div>
                            <div class="searchTitleTxt" v-show="searchRult == 2">您是否想要找这些</div>
                            <div class="sortBtnList" :class="searchRult==3?'sortBtnListAct':''" v-show="goodsListData.length > 0">
                                <div class="volumeBox" @click="clickSaleBtn()">
                                    <div class="volumeBtn" :class="goodsSortOne && sortMethod ? 'selectBtnColor' : ''">销量</div>
                                </div>
                                <div @click="priceSort">
                                    <div class="priceBtn">
                                        <div class="priceTxt" :class="!goodsSortOne && sortMethod ? 'selectBtnColor' : ''">价格</div>
                                        <img :src="sortMethod==''||sortMethod==3?icon.filter:(sortMethod==1?icon.filterUp:icon.filterDown)" alt="">
                                    </div>
                                </div>
                            </div>
                        </div>
                        <van-list
                                class="goodsVantList"
                                v-model="loading"
                                :finished="finished"
                                :immediate-check="immediate"
                                @load="onLoad"
                        >
                            <div :style="{minHeight:goodsListHeight+'px'}">
                                <div class="goodsList" v-if="!isNetworkError" ref="refGoodsList">
                                    <div class="goodsItem"  v-for="(goodsItem,goodsIndex) in goodsListData"
                                         :key="'goodsIndex'+goodsIndex" @click="goToProduct(goodsItem, goodsIndex)">
                                        <div class="goodsImg">
                                            <img v-lazy="goodsItem.spuPicUrl" alt="" :key="goodsItem.spuPicUrl">
                                            <div class="showActivitySpu" v-show="goodsItem.activitySpuId">秒杀</div>
                                            <div class="outSellBox" v-show="goodsItem.storeNumber==0&&!goodsItem.activitySpuId">补货中</div>
                                            <div class="promotionBox" v-show="goodsItem.actType!=null" :class="goodsItem.actType==0||goodsItem.actType==1?'greenPromotionBox':'redPromotionBox'">{{goodsItem.actTag}}</div>
                                            <div class="onlyRemainTxt" v-show="goodsItem.storeNumber<=goodsItem.threshold&&goodsItem.storeNumber!=0&&!goodsItem.activitySpuId">仅剩{{goodsItem.storeNumber}}件</div>
                                        </div>
                                        <div class="goodsInfo">
                                            <div class="goodsName">{{goodsItem.spuName}}</div>
                                            <div class="labelList">
                                                <div class="labelItem">
                                                    <span :class="goodsItem.shopName.length>8?'labelItemOver':''">{{goodsItem.shopName}}</span>
                                                </div>
                                                <div class="labelItem ticketLabelItem" v-show="goodsItem.couponFlag==1">券</div>
                                            </div>
                                            <div class="goodsBottomInfo">
                                                <div class="goodsPrice">
                                                    <div class="priceAndSale">
                                                        <div class="goodsPriceNum">￥{{goodsItem.skuPrice|priceFilters}}</div>
                                                        <!-- <div class="saleNum" v-show="themeCurrent!='1'">月售{{goodsItem.virtualSale | threshold}}</div> -->
                                                    </div>
                                                    <div class="priceAndSale" v-show="themeCurrent=='1'">
                                                        <div class="lineGoodsPriceNum"  v-show="goodsItem.priceLine">￥{{goodsItem.priceLine|priceFilters}}</div>
                                                        <!-- <div class="saleNum saleNum-l">月售{{goodsItem.virtualSale | threshold}}</div> -->
                                                    </div>
                                                    <div class="lineGoodsPriceNum"  v-show="goodsItem.priceLine&&themeCurrent!='1'">￥{{goodsItem.priceLine}}</div>
                                                </div>
                                                <div class="addBtn" @click.stop="addGoods(goodsItem)">
                                                    <div class="cartNumBox" v-show="goodsItem.cartCount!=0" :class="goodsItem.cartCount>99?'cartNumBigBox':''">
                                                        <div class="cartNum">{{goodsItem.cartCount}}</div>
                                                    </div>
                                                    <img src="@/assets/images/themeImg/icon_add.png" alt="">
                                                    <!-- <img v-show="goodsItem.storeNumber!=0" src="@/assets/images/mall/mall_icon_add.png" alt=""> -->
                                                    <!-- <img v-show="goodsItem.storeNumber==0" src="@/assets/images/mall/mall_icon_add_gray.png" alt=""> -->
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="nullDataBox" v-if="goodsFirstLoaded && searchRult==3&&!isNetworkError">
                                    <img class="nullDataImg" src="@/assets/images/default/no_searching_img.png" alt="">
                                    <div class="nullDataTxt">暂无相关搜索结果</div>
                                </div>
                                <div style="padding-top:100px;height:300px" v-if="isNetworkError">
                                    <NetworkError subTitle=""></NetworkError>
                                </div>
                            </div>

                            <div class="finishedBox" ref="finishedBox" :style="{paddingBottom:this.$stbHeight+'px'}" v-show="finished&&searchRult!=3&&!isNetworkError&&isShowFanishLogo" >
                                <img src="@/assets/images/mall/mall_logo.png" alt="">
                            </div>
                        </van-list>
                    </div>

                </van-tab>
                <van-tab title="店铺" name="1">
                    <div class="goods-content" :style="{height: contentListH +'px'}" ref="shopContent">
                        <div class="titleSortBtn" v-show="!isNetworkError">
                            <div class="searchTitleTxt" v-show="shopListData.length > 0">为您找到以下店铺</div>
                            <div class="sortBtnList" v-show="shopListData.length > 0">
                                <div class="volumeBox" @click="clickShopSaleBtn()">
                                    <div class="volumeBtn" :class="shopSortOne && shopSortMethod ? 'selectBtnColor' : ''">销量</div>
                                </div>
                                <div @click="shopScoreSort">
                                    <div class="priceBtn">
                                        <div class="priceTxt" :class="!shopSortOne && shopSortMethod ? 'selectBtnColor' : ''">评分</div>
                                        <img :src="shopSortMethod==''||shopSortMethod==2?icon.filter:(shopSortMethod==3?icon.filterUp:icon.filterDown)" alt="">
                                    </div>
                                </div>
                            </div>
                        </div>
                        <van-list
                                class="goodsVantList"
                                v-model="shopLoading"
                                :finished="shopFinished"
                                :immediate-check="immediate"
                                @load="shopOnLoad"
                        >
                            <div :style="{minHeight:goodsListHeight+'px'}">
                                <div class="shopList" v-if="!isNetworkError" ref="refShopList">
                                    <div class="shopItem"  v-for="(shopItem,shopIndex) in shopListData" :key="'shopIndex'+shopIndex">
                                        <div class="shopItemTop" @click="clickGoShop(shopItem)">
                                            <img class="sTopImg" :src="shopItem.shopPicUrl" alt="">
                                            <div class="sTopContent">
                                                <div class="sTopTitle">{{shopItem.shopName}}</div>
                                                <div  class="sTopDes">
                                                    <div class="shopStarBox" v-if="shopItem.shopLevel !=0">
                                                        <img v-for="index in shopItem.shopLevel" :key="'shopLevelItem'+index"
                                                             src="@/assets/images/common/icon_evaluate_red.png" alt="">
                                                        <img v-for="index in 5-shopItem.shopLevel" :key="'noShopLevelItem'+index"
                                                             src="@/assets/images/common/icon_evaluate_gray.png" alt="">
                                                        <div class="starScore">{{shopItem.shopLevel}}</div>
                                                    </div>
                                                    <div v-else class="shopStarBox">
                                                        <img src="@/assets/images/common/icon_evaluate_gray.png" alt="">
                                                        <div class="starScore noLevel">无评分</div>
                                                    </div>
                                                    <div class="sTopMonthSales">月售{{shopItem.orderCount}}</div>
                                                </div>

                                            </div>
                                            <div class="shopBtn">进店</div>
                                        </div>

                                        <div class="shopItemContent">
                                            <div class="shopGoodItem"
                                                 v-for="(sGItem, sGItemIndex) in shopItem.goodsList"
                                                 :key="'gItemIndex' + sGItemIndex"
                                                 @click="clickGoGoodDetail(sGItem)">
                                                <img :src="sGItem.spuPic " alt="">
                                                <div class="showActivitySpu" v-show="sGItem.activitySpuId">秒杀</div>
                                                <div class="outSellBox" v-show="sGItem.storeNumber<=0&&!sGItem.activitySpuId">补货中</div>
                                                <div class="promotionBox" v-show="sGItem.actType!=null" :class="sGItem.actType==0||sGItem.actType==1?'greenPromotionBox':'redPromotionBox'">{{sGItem.actTag}}</div>
                                                <!-- <div class="sGItemTopLab">99元3件</div> -->
                                                <div class="priceLab">¥{{sGItem.price}}</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="nullDataBox" v-if="shopFirstLoaded && shopListData.length === 0 &&!isNetworkError">
                                    <img class="nullDataImg" src="@/assets/images/default/no_searching_img.png" alt="">
                                    <div class="nullDataTxt">暂无相关搜索结果</div>
                                </div>
                                <div style="padding-top:100px;height:300px" v-if="isNetworkError">
                                    <NetworkError subTitle=""></NetworkError>
                                </div>
                            </div>
                            <div class="finishedBox" ref="finishedBox" :style="{paddingBottom:this.$stbHeight+'px'}" v-show="shopFinished && !isNetworkError && shopListData.length > 0 && isShowFanishLogo">
                                <img src="@/assets/images/mall/mall_logo.png" alt="">
                            </div>
                        </van-list>
                    </div>
                </van-tab>
            </van-tabs>
            <!--输入界面-->
            <div class="fixedSearchBox" v-show="isShowFixedSearch">
                <div class="flex-full" :style="{paddingTop:this.$stbHeight+'px'}">
                    <div slot="left" class="leftBar">
                        <!-- <div class="backIcon" @click="goBack()">
                            <img src="@/assets/images/common/nav_return_black.png" alt="">
                        </div> -->
                        <form class="searchInput" @submit.prevent="formSubmit" action="javascript:return true">
                            <input class="FixedsearchInput" :class="themeCurrent=='1'?'FixedsearchInput-l ':''" ref="inputSearch" @focus="searchInputFocus($event)" @blur="focusState = false" v-focus="focusState" v-model="searchKey" @keydown.13="goodsSearchBtn(false)" type="search" maxlength="30" :placeholder="placeholderWord.length > 0 ? placeholderWord : '搜索好物'">
                        </form>
                        <img class="voiceIcon" src="@/assets/images/newSlicing/coupon/nav_searchbar_icon_closed.png" v-show="searchKey.length!=0" alt="" @click="emptySearch">
                        <div class="searchBtn" @click="goBack()">取消</div>
                    </div>
                </div>
                <!-- 无语音搜索、语音搜索结果、无热搜且无搜索历史 -->
                <div class="historyTitleContent" v-show="(!isShowSpeechDiv && !isNoRecognizeResult) && !(SearchHisList.length ==0 && hotSearchWord.length > 0)">
                    <div class="historyTitle">
                        <div class="historyTxt" :class="themeCurrent=='1'?'historyTxt-l ':''">搜索记录</div>
                        <img :class="themeCurrent=='1'? 'closeBtn-l' : 'closeBtn'" @click="closeHistoryBtn" v-show="SearchHisList.length!=0" src="@/assets/images/mall/search_delete.png" alt="">
                    </div>
                    <div class="historyList" ref="historyList" :class="moreHistory?'':'moreHistoryList'" :style="{'visibility': (historyWordVisible ? 'visible' : 'hidden')}">
                        <div class="historyBox" ref="historyBox">
                            <div class="historyItem" :class="themeCurrent=='1'?'historyItem-l ':''" v-for="(SearchHisItem,index) in SearchHisList"
                                 :key="'SearchHisItem'+index" @click="historyItemBtn(SearchHisItem)">{{SearchHisItem}}</div>
                            <div class="moreHistoryBox" :class="themeCurrent=='1'? 'moreHistoryBox-l' : ''" @click="moreHistoryBtn" v-show="SearchHisList.length!=0 && isShowMoreHistory">
                                <img src="@/assets/images/mall/icon_searchArrow.png" alt="" :class="moreHistory?'moreHistoryIcon':''">
                            </div>
                        </div>
                    </div>
                    <div class="noHistoryBox" v-show="SearchHisList.length==0">
                        <div class="moreHistoryTxt noMoreHistoryTxt">暂无历史</div>
                    </div>
                </div>

                <!-- <div class="moreHistoryBox" @click="moreHistoryBtn" v-show="SearchHisList.length!=0&&isShowMoreHistory&&!isShowSpeechDiv&&!isNoRecognizeResult">
                    <div class="moreHistoryTxt">更多历史搜索</div>
                    <img src="@/assets/images/mall/icon_filter_down.png" alt="" :class="moreHistory?'moreHistoryIcon':''">
                </div> -->
                <div class="topSearchContent" v-show="hotSearchWord.length > 0 && !isShowSpeechDiv && !isNoRecognizeResult">
                    <div class="topSearch">
                        <div class="historyTxt" :class="themeCurrent=='1'?'historyTxt-l ':'' ">热门搜索</div>
                    </div>
                    <div class="topSearchList">
                        <div class="historyBox">
                            <div class="historyItem" :class="themeCurrent=='1'?'historyItem-l':''" v-for="(topSearchItem,index) in hotSearchWord"
                                 :key="'topSearchItem'+index" @click="historyItemBtn(topSearchItem)">{{topSearchItem}}</div>
                        </div>
                    </div>
                </div>

                <!-- <div class="speechSearchDiv" v-show="isShowSpeechDiv && !isNoRecognizeResult">
                    <div class="tryToSay" v-show="!isShowSpeechResult">试试这样说</div>
                    <div class="speechexample" v-show="!isShowSpeechResult">“防晒” <br /> “油”<br />“我想要买榴莲”<br />“水果”<br />...</div>
                    <div class="showSpeechResult" v-show="isShowSpeechResult">{{this.recognizeResult}}</div>
                    <img class="speechGifDiv" src="@/assets/images/mall/search_voice.gif" alt="" v-show="!isShowSpeechResult">
                    <img class="speechGifDiv" src="@/assets/images/mall/search_voice.png" alt="" v-show="isShowSpeechResult">
                </div>
                <div class="noRecognizeResultDiv" v-show="isNoRecognizeResult">
                    <img class="noRecognizeResultimg" src="@/assets/images/mall/no_voicesearch_img.png" alt="">
                    <div class="noRecognizeResultText">未检测到语音</div>
                </div>

                <div class="voiceBtn" @touchstart="goToSpeech" @touchend="stopSpeech" ref="voiceBtnKeyBoard" v-show="isShowVoiceBtn">
                    <img class="voiceBtnImg" src="@/assets/images/mall/search_red.png" alt="" v-show="!isPressSpeechBtn">
                    <div class="voiceBtnText" v-show="!isPressSpeechBtn">按住说话</div>
                    <img class="voiceBtnImg" src="@/assets/images/mall/search_white.png" alt="" v-show="isPressSpeechBtn">
                    <div class="voiceBtnTextInPress" v-show="isPressSpeechBtn" >按住说话</div>
                </div> -->
            </div>
        </div>
    </div>
</template>

<script>
    import {getSearchHisService,getSearchHotWordService,dropSearchHisService,addGoodsService,getSkuTypeNumService,correctKeywordService} from "../../services/goodsService";
    import { Toast,Dialog } from "vant";
    import { mapActions, mapState } from "vuex";
    import Vue from 'vue';
    import SpeechRecognizerDialog from "../../common/currency/SpeechRecognizerDialog";
    import NetworkError from '../../common/networkError/NetworkError'
    export default {
        name: "goodsSearch",
        data(){
            return{
                isShowVoiceBtn:true, //语音搜索入口
                isPressSpeechBtn:false, //按下长按说话按钮
                isShowSpeechDiv:false,  //语音搜索按下时显示语音提界面
                isNoRecognizeResult:false, //没有识别结果
                isShowSpeechResult:false, //显示语音识别结果
                isHasAudioPermissions:false, //判断是否有语音权限（Android用）
                clientHeight:0,//页面高度
                goodsListHeight:0, //商品高度
                SearchHisList : [], //历史记录列表
                moreHistory:false, // 查看历史记录标识
                isShowFixedSearch:true, //是否显示输入界面 false 不显示 true 显示
                isShowPopup:false, //是否显示删除历史界面弹窗
                screenKeyboardHeight:0, //键盘高度

                loading: false, //是否正在加载
                finished: false, //结束上拉加载
                immediate:false,

                searchKey:"", //搜索的内容
                shopCode:"-1",  //店铺内搜索的店铺CODE
                goodsListData:[],// 搜索的商品列表
                isLastPage:false, //是否是最后一页
                setVantHeight:{},//设置vantList的高度
                focusState:true, //焦点状态
                isShowHistory: true, //是否展示过搜索页面，用于搜索结果页返回逻辑

                sortMethod:"", // 3 销量 1 从小到大 2从大到小
                isPriceSort:false, // false 从小到大 true 从大到小
                icon:{
                    filter:require("@/assets/images/mall/icon_filter_new.png"),
                    filterDown:require("@/assets/images/mall/icon_filter_down_new.png"),
                    filterUp:require("@/assets/images/mall/icon_filter_up_new.png"),
                },

                searchRult: 1, //1 准确 2 相似 3 无结果
                sendVal:false, //语音搜索状态控制
                recognizeResult:"正在聆听",
                skuTypeNum:0, //购物车数量
                correctKeyword:'',

                isShowMoreHistory:true,
                goodScrollTop:0,
                rows:10,
                changeInfoRows:0,
                isNetworkError:false,
                pressTimer:'',
                themeCurrent: this.$curTheme,
                //计算完搜索记录词是否显示展开
                historyWordVisible: false,
                historyKeyWordAll: [],
                historyKeyWordSection: [],
                // 占位词
                placeholderWord:'',
                //热搜词
                hotSearchWord: [],
                /**
                 * 店铺维度增加
                 */
                tabIndex: "0",
                isShowFanishLogo:true,
                //内容滚动区域高度
                contentListH: 0,
                //店铺维度商品宽度
                shopItmW: 0,
                shopListData: [],
                shopGoodList: [1, 2, 3],
                shopLoading: false,
                shopFinished: false,
                shopPage: 1,
                shopSortMethod: '', //1为销量由低到高，2为销量由高到低，3为评分从低到高，4为评分从高到低。
                currentShopCount: 0,
                shopScrollTop:0,

                goodsSortOne: true,
                shopSortOne: true,
                goodsFirstLoaded: false,
                shopFirstLoaded: false,
            }
        },
        components: {
            'dialog-bar' : SpeechRecognizerDialog,
            NetworkError,
        },
        filters:{
            threshold(data){
                return data >= 10000 ?  (data/10000>=10?'9万+':Math.floor(data / 10000) + '万+') : data
            },
            priceFilters(data){
                return data >= 10000 ? Math.floor(data/10000*100)/100+ '万' : data
            }
        },
        watch: {
            sortMethod:function (val) {
                if (val=="") {
                    return;
                }
                // this.rows = 10;
                this.$store.commit("goodsSearchPageNumberChange", {
                    custPageNum: true,
                    pageNumber: 1
                });
                this.goodsSearch(1);
            },
            shopSortMethod:function (val) {
                if (val=="") {
                    return;
                }
                //店铺页排序发生改变
                this.goodsSearch(1);
            }
        },
        computed: mapState({
            pageNumber: state => state.productPage.goodsSearchPageNumber,
            goodsSearchEventBackButtonFlag: state => state.goBack.goodsSearchEventBackButtonFlag,
        }),
        directives: {
            focus: {
                update: function (el, {value}) {
                    if (value) {
                        el.focus()
                    }
                }
            }
        },
        created() {
            this.$nextTick(()=>{
                this.clientHeight = this.$refs.flexPage.clientHeight;
                this.contentListH = this.clientHeight - this.$stbHeight - 44 - 44;
                this.goodsListHeight = this.clientHeight-this.$refs.refGoodsList.offsetTop-43-46;
                // console.log(this.$refs.refGoodsList.offsetTop+32);
            })
            if (this.$deciceType=='phone'){
                require('../../assets/css/phone/superiorProduct/goodsSearch.scss')
            }else if (this.$deciceType=='portraitPad'){
                require('../../assets/css/portraitPad/superiorProduct/goodsSearch.scss')
            }
        },
        mounted(){
            // this.forbidBodyScroll(); //禁止滑动
            // this.getSearchHis(); //获取搜索历史记录
            this.pressTimer = null;
        },
        activated(){
            window.addEventListener("native.keyboardshow",this.keyboardshow);
            window.addEventListener("native.keyboardhide", this.keyboardhide);
            this.themeCurrent = this.$curTheme;
            this.isShowSpeechDiv=false;
            this.isNoRecognizeResult = false;
            if(this.$route.params.isRefresh){
                this.searchKey = "";
                this.isShowHistory = true;
                this.tabIndex = '0';
                this.showFixedSearch();
            }else{
                this.changeInfoRows = this.goodsListData.length;
                this.currentShopCount = this.shopListData.length;
                this.updateSearchData();
                this.setScrollTop();
            }
            //显示搜索界面时候 禁止滑动
            // if (this.isShowFixedSearch){
            //     this.forbidBodyScroll();
            // }

            if (this.$route.params.shopCode){
                this.shopCode = this.$route.params.shopCode;
            }else{
                this.shopCode = "-1";
            }
            if(this.$route.params.speechTxt){
                this.focusState = false;
                this.isShowHistory = false;
                this.isShowFixedSearch = false;
                this.goodsListData = [];
                this.shopListData = [];
                this.historyItemBtn(this.$route.params.speechTxt);
            }
            if(this.$route.params.placeholderWord || this.$route.params.placeholderWord == ''){
                this.placeholderWord = this.$route.params.placeholderWord;
            }
            this.getSkuTypeNum();
            // warning 临时屏蔽店铺页热门搜索词功能
            if (this.shopCode != '-1') {
                this.hotSearchWord = [];
            }
            else {
                this.getSearchHotWord();
            }
        },
        beforeRouteLeave(to, from, next) {
            window.removeEventListener("native.keyboardshow", this.keyboardshow);
            window.removeEventListener("native.keyboardhide", this.keyboardhide);
            this.recordScrollTop();
            next();
        },
        methods: {
            ...mapActions(["getGoodsForSearch","getNewGoodsForSearch", "getGoodsForShopSearch"]),
            //去左右空格
            cleanSpace(s){
                return s.replace(/(^\s*)|(\s*$)/g, "");
            },
            //提示toast
            toastPopMsg(msg){
                if (msg && msg.length > 0) {
                    Toast(msg);
                }
            },
            goToGoodsSearch(resultcatch){
                this.historyItemBtn(resultcatch);
            },
            /**
             * 键盘弹出事件
             */
            keyboardshow(e) {
                this.$refs.voiceBtnKeyBoard.style.bottom = e.keyboardHeight + 12 +"px";
                this.screenKeyboardHeight = e.keyboardHeight + 12;
                this.isShowSpeechDiv=false;
                this.isNoRecognizeResult = false;
                this.isShowSpeechResult = false;
                this.isShowVoiceBtn = true;
            },

            /**
             * 键盘隐藏事件
             */
            keyboardhide(e) {
                this.$refs.voiceBtnKeyBoard.style.bottom = this.screenKeyboardHeight + "px";
                if (!this.sendVal && !this.isShowSpeechDiv) {
                    this.isShowVoiceBtn = false;
                }
            },
            /**
             * 查询购物车数量
             * ownerCode  Number  业主CODE
             */
            getSkuTypeNum(isAddGoods=false){
                let paramData = {
                    ownerCode:this.$userInfo.OWNER_CODE
                }
                getSkuTypeNumService(paramData).then(res=>{
                    if (res.resultCode == 0) {
                        this.skuTypeNum = res.data.skuTypeNum;
                    }
                }).catch(error=>{
                })
            },
            speechResultSuccess(result){
                console.log("success "+result);

            },
            speechResultError(message){
                // _this.sendVal = false;
                console.log("err" + message);
            },
            bodyScroll(event){
                event.preventDefault();
            },
            // //禁止滑动
            // forbidBodyScroll(){
            //     document.querySelector(".post-wrapper").addEventListener('touchmove', this.bodyScroll, { passive: false });
            // },
            // //恢复滑动
            // allowBodyScroll(){
            //     document.querySelector(".post-wrapper").removeEventListener('touchmove', this.bodyScroll, { passive: false });
            // },
            /**
             * 获取搜索历史记录
             * ownerCode Number 业主CODE
             */
            getSearchHis(){
                let that = this;
                let paramData = {
                    ownerCode: this.$userInfo.OWNER_CODE
                }
                getSearchHisService(paramData).then(res => {
                    if (res.resultCode==0){
                        this.SearchHisList = res.data;
                        this.historyKeyWordAll = res.data;
                        this.$nextTick(()=>{
                            that.historyWordVisible = false;
                            if(that.$refs.historyBox.clientHeight>that.$refs.historyList.clientHeight){
                                that.isShowMoreHistory = true;
                                that.isShowSpeechDiv=false;
                                that.historyKeyWordSection = that.historyKeyWordAll.slice(0, 1);
                                that.calculateHistoryShowMore(0);
                            }else{
                                that.isShowMoreHistory = false;
                                that.historyWordVisible = true;
                            }
                        })
                    }
                }).catch(error => {
                });
            },
            // 获取搜索热搜词列表
            getSearchHotWord(){
                let that = this;
                let paramData = {
                    areaCode: this.$userInfo.AREA_CODE
                }
                getSearchHotWordService(paramData).then(res => {
                    if (res.resultCode==0 && res.data){
                        that.hotSearchWord = res.data;
                    }
                }).catch(error => {
                    console.log('搜索热搜词列表获取失败： ' + error);
                });
            },

            //计算历史搜索词是否显示更多
            calculateHistoryShowMore(index) {
                if (this.isShowMoreHistory) {
                    if (this.historyKeyWordAll && index < this.historyKeyWordAll.length) {
                        this.SearchHisList = this.historyKeyWordAll.slice(0, index+1);

                        this.$nextTick(()=>{
                            if(this.$refs.historyBox.clientHeight > this.$refs.historyList.clientHeight){
                                this.SearchHisList = this.historyKeyWordSection;
                                this.historyWordVisible = true;
                            }else{
                                this.historyKeyWordSection = this.SearchHisList;
                                this.calculateHistoryShowMore(index+1);
                            }
                        });
                    }
                }
            },
            //点击历史记录标签进行搜索
            historyItemBtn(searchKey){
                this.searchKey = searchKey;
                this.goodsSearch(1);
            },
            //点击查看更多历史记录按钮
            moreHistoryBtn(){
                this.moreHistory = !this.moreHistory;
                this.SearchHisList = this.moreHistory ? this.historyKeyWordAll : this.historyKeyWordSection;

            },
            //点击删除历史记录按钮
            closeHistoryBtn(){
                Dialog.confirm({
                    message: '确定删除全部历史记录吗？'
                }).then(() => {
                    // on confirm
                    this.dropSearchHis();
                }).catch(() => {
                    // on cancel
                });
            },
            /**
             * 删除历史记录
             * ownerCode  Number 业主CODE
             */
            dropSearchHis(){
                let paramData = {
                    ownerCode: this.$userInfo.OWNER_CODE
                }
                dropSearchHisService(paramData).then(res => {
                    if (res.resultCode==0){
                        this.SearchHisList = [];
                        this.toastPopMsg('删除成功');
                    }
                    else {
                        this.toastPopMsg('删除失败，请检查网络');
                    }
                }).catch(error => {
                    this.toastPopMsg('删除失败，请检查网络');
                });
            },
            emptySearch(){
                this.searchKey = "";
            },
            //重置搜索
            showFixedSearch(){
                // this.searchKey = "";
                this.sortMethod = "";
                this.shopSortMethod = '';
                this.goodsListData = [];
                this.shopListData = [];
                this.isPriceSort = false;
                this.isShowFixedSearch = true;
                // this.forbidBodyScroll();
                this.getSearchHis();
                this.isShowHistory = true;
                this.moreHistory = false;
                this.focusState = true;
                this.goodsFirstLoaded = false;
                this.shopFirstLoaded = false;
                // this.rows=10;
                console.log("【输入框】showFixedSearch ")
                this.$forceUpdate();
            },
            //搜索输入框获取焦点
            searchInputFocus(e){
                var obj = e.srcElement;
                console.log("【输入框】searchInputFocus "+obj.value.length)
                //obj.selectionEnd = obj.value.length

                //modify for ZHSQYYPT-7226
                var value = obj.value;
                obj.value = ""
                obj.value = value
            },
            //点击搜索叉号按钮
            voiceIconBtn(){
                this.searchKey = "";
                this.sortMethod = "";
                this.shopSortMethod = '';
                this.goodsListData = [];
                this.shopListData = [];
                this.isPriceSort = false;
                this.isShowFixedSearch = true;
                // this.forbidBodyScroll();
                this.getSearchHis();
                this.isShowHistory = true;
                this.moreHistory = false;
                this.focusState = true;
                this.goodsFirstLoaded = false;
                this.shopFirstLoaded = false;
                this.rows=10;

                this.$forceUpdate();
            },
            //价格排序
            priceSort(){
                this.goodsSortOne = false;
                if (!this.isPriceSort){
                    this.sortMethod = 1;
                }else{
                    this.sortMethod = 2;
                }
                this.isPriceSort = !this.isPriceSort;
            },
            //销量排序
            clickSaleBtn(){
                this.goodsSortOne = true;
                this.sortMethod = 3;
                this.isPriceSort= false;
            },
            //评分排序
            shopScoreSort(){

                //1为销量由低到高，2为销量由高到低，3为评分从低到高，4为评分从高到低。
                if (this.shopSortMethod != 3) {
                    this.shopSortMethod = 3;
                }
                else {
                    this.shopSortMethod = 4;
                }


                this.shopSortOne = false;
            },
            //店铺维度销量排序
            clickShopSaleBtn(){
                this.shopSortOne = true;
                //1为销量由低到高，2为销量由高到低，3为评分从低到高，4为评分从高到低。
                this.shopSortMethod = 2;
            },
            //点击搜索按钮
            goodsSearchBtn(isHide=true){
                // this.focusState = false;
                if(!isHide){
                    this.$refs.inputSearch.blur();
                    this.focusState = false;
                }
                //去除两边空格
                this.searchKey = this.cleanSpace(this.searchKey);
                // console.log('过滤后的搜索词为：' + this.searchKey)
                if (this.searchKey == "" && this.placeholderWord == ''){
                    this.toastPopMsg('请输入搜索内容');
                    this.focusState = true;
                    return;
                }

                this.goodsSearch(1);
            },
            /**
             * 商品搜索
             * @param
             * searchKey Number 搜索内容
             * areaCode  Number 小区CODE
             * ownerCode Number  业主CODE
             * page      Number 页数
             * rows      Number 个数
             * shopCode  Number 店铺CODE
             * sortMethod  Number  排序方式
             * */
            goodsSearch(pageNumber,isRefresh=true){
                if (window.navigator.onLine){
                    this.isNetworkError = false;
                }else{
                    this.isNetworkError = true;
                    this.isShowFixedSearch = false;
                    // this.allowBodyScroll();
                    this.isShowSpeechDiv=false;
                    return;
                }
                if (this.searchKey.length>30){
                    this.searchKey = this.searchKey.slice(0,29)
                }
                //判断更新商品页、店铺页
                if (this.tabIndex == '0') {
                    //加载商品页
                    this.goodPageSearch(pageNumber,isRefresh);
                }
                else {
                    //加载店铺页
                    this.shopPageSearch(this.shopPage, isRefresh);
                }
            },
            //商品页搜索
            goodPageSearch(pageNumber, isRefresh) {
                let paramData = {
                    searchKey:this.searchKey.length == 0 ? this.placeholderWord : this.searchKey,
                    areaCode:this.$userInfo.AREA_CODE,
                    ownerCode:this.$userInfo.OWNER_CODE,
                    page:pageNumber,
                    rows:this.rows,
                    shopCode:this.shopCode,
                    sortMethod:this.sortMethod
                }
                this.getGoodsForSearch(paramData).then(res => {
                    if (res.resultCode == 0&&!this.focusState){
                        if(res.data.list!=null&&res.data.list.length!=0){
                            res.data.list.map((item,index)=>{
                                if(index%2==0){
                                    item.spuPicUrl = item.spuPicUrl.replace(/xwjfile.juhaolian.cn/,'xwjfilea.juhaolian.cn')
                                }
                                Vue.prototype.$isHasLocalImage(item.spuPicUrl,function(localImgUrl){
                                    item.spuPicUrl = localImgUrl;
                                })
                            })
                        }
                        this.isShowFixedSearch = false;
                        // this.allowBodyScroll();
                        if (isRefresh) {
                            this.goodsFirstLoaded = true;
                            this.$refs.goodContent.scrollTop = 0;
                            this.searchRult = res.data.searchRult;
                            if (this.searchRult == 3){
                                this.goodsListData = [];
                            } else{
                                this.goodsListData = res.data.list;
                            }
                            this.$store.commit("goodsSearchPageNumberChange", {
                                custPageNum: true,
                                pageNumber: 2
                            });
                        }else{
                            this.goodsListData = this.goodsListData.concat(res.data.list)
                        }
                        this.loading = false;
                        this.finished = res.data.isLastPage;
                    }else{
                        if (res.resultCode != 0) {
                            this.toastPopMsg(res.msg);
                        }
                    }
                    this.isShowSpeechDiv=false;
                }).catch(error => {
                    this.isNetworkError = true;
                    this.isShowFixedSearch = false;
                    this.isShowSpeechDiv=false;
                    // this.allowBodyScroll();
                });
            },
            //店铺页搜索
            shopPageSearch(pageNumber, isRefresh) {
                let paramData = {
                    searchKey:this.searchKey.length == 0 ? this.placeholderWord : this.searchKey,
                    areaCode:this.$userInfo.AREA_CODE,
                    // ownerCode:this.$userInfo.OWNER_CODE,
                    page:isRefresh ? 1 : this.shopPage,
                    rows:this.rows,
                    // shopCode:this.shopCode,
                    sortMethod:this.shopSortMethod
                }
                this.getGoodsForShopSearch(paramData).then(res => {
                    if (res.resultCode == 0 && !this.focusState){
                        if (isRefresh) {
                            this.shopListData = [];
                            this.shopFirstLoaded = true;
                        }
                        this.isShowFixedSearch = false;
                        if(res.data.records && res.data.records.length > 0){
                            // // #mark yz 图片展示优化，先屏蔽
                            // res.data.list.map((item,index)=>{
                            //     if(index%2==0){
                            //         item.spuPicUrl = item.spuPicUrl.replace(/xwjfile.juhaolian.cn/,'xwjfilea.juhaolian.cn')
                            //     }
                            //     Vue.prototype.$isHasLocalImage(item.spuPicUrl,function(localImgUrl){
                            //         item.spuPicUrl = localImgUrl;
                            //     })
                            // })
                            // this.allowBodyScroll();
                            if (isRefresh) {
                                this.$refs.shopContent.scrollTop = 0;

                                this.shopListData = res.data.records;
                            }else{
                                this.shopListData = this.shopListData.concat(res.data.records)
                            }
                        }
                        this.shopPage = res.data.current + 1;
                        this.shopLoading = false;
                        this.shopFinished = res.data.current >= res.data.pages;
                    }else{
                        this.toastPopMsg(res.msg)
                    }
                    this.isShowSpeechDiv=false;
                }).catch(error => {
                    this.toastPopMsg(error)
                    this.isNetworkError = true;
                    this.isShowFixedSearch = false;
                    this.isShowSpeechDiv=false;
                    // this.allowBodyScroll();
                });
            },
            updateSearchData(){
                if(this.searchKey.length==0){
                    return;
                }
                //判断更新商品页、店铺页
                if (this.tabIndex == '0') {
                    //刷新商品页
                    this.updateSearchGoodsPage();
                }
                else {
                    //刷新店铺页
                    this.updateSearchShopPage();
                }
            },
            //刷新搜索商商品
            updateSearchGoodsPage() {
                let paramData = {
                    searchKey:this.searchKey.length == 0 ? this.placeholderWord : this.searchKey,
                    areaCode:this.$userInfo.AREA_CODE,
                    ownerCode:this.$userInfo.OWNER_CODE,
                    page:1,
                    rows:this.changeInfoRows,
                    shopCode:this.shopCode,
                    sortMethod:this.sortMethod
                }
                this.getNewGoodsForSearch(paramData).then(res => {
                    if (res.resultCode == 0){
                        this.goodsListData.map((item,index)=>{
                            res.data.list.map((resItem)=>{
                                if(item.spuCode == resItem.spuCode){
                                    item.skuPrice = resItem.skuPrice;
                                    item.storeNumber = resItem.storeNumber;
                                }
                            })
                        })
                        // this.$store.commit("goodsSearchPageNumberChange", {
                        //     custPageNum: true,
                        //     pageNumber: 2
                        // });
                    }else{
                        this.toastPopMsg(res.msg)
                    }
                }).catch(error => {
                });
            },
            //刷新搜索商铺页
            updateSearchShopPage() {
                let paramData = {
                    searchKey:this.searchKey.length == 0 ? this.placeholderWord : this.searchKey,
                    areaCode:this.$userInfo.AREA_CODE,
                    // ownerCode:this.$userInfo.OWNER_CODE,
                    page:1,
                    rows:this.currentShopCount,
                    // shopCode:this.shopCode,
                    sortMethod:this.shopSortMethod
                }

                this.getGoodsForShopSearch(paramData).then(res => {
                    if (res.resultCode == 0){
                        this.shopListData.map((item)=>{
                            res.data.list.map((resItem)=>{
                                if(item.shopCode  == resItem.shopCode ){
                                    let count =item.goodsList.length > resItem.goodsList.length ? resItem.goodsList.length : item.goodsList.length;
                                    for (let i = 0; i < count; i++) {
                                        const element = resItem.goodsList[i];
                                        const itemGood = item.goodsList[i];
                                        if (element.spuCode === itemGood.spuCode) {
                                            itemGood.price = element.price;
                                        }
                                    }
                                }
                            })
                        })
                    }else{
                        this.toastPopMsg(res.msg)
                    }
                }).catch(error => {
                });
            },
            //商品加载更多
            onLoad() {
                if (this.finished) {
                    return;
                }
                this.goodsSearch(this.pageNumber,false);
            },
            //店铺维度加载更多
            shopOnLoad() {
                if (this.shopFinished) {
                    return;
                }
                this.goodsSearch(this.shopPage,false);
            },
            tabChange() {
                console.log('当前tabIndex： ' + this.tabIndex);
                if (this.isShowFixedSearch) {
                    //显示的非结果页
                    return;
                }
                //判断更新商品页、店铺页
                if (this.tabIndex == '0') {
                    if (this.goodsListData.length > 0) {
                        //刷新商品页
                        this.updateSearchGoodsPage();
                    }
                    else {
                        this.goodsSearch(1);
                    }
                }
                else {
                    if (this.shopListData.length > 0) {
                        //刷新店铺页
                        this.updateSearchShopPage();
                    }
                    else {
                        this.goodsSearch(1);
                    }
                }
                this.recordScrollTop(true);
            },
            clickGoShop(shopItem) {
                this.$router.push({
                    name: 'shopPage',
                    params: { shopCode: shopItem.shopCode }
                });
            },
            clickGoGoodDetail(goodItem) {
                this.goToProduct(goodItem);
            },
            /**
             * 添加购物车
             * @param
             * ownerCode Number  业主CODE
             * spuCode   Number  商品CODE
             * skuNo     Number  商品分类CODE
             * shopCode  Number  店铺CODE
             * skuCount  Number  商品数量
             * mark      Number  业主App标识
             */
            addGoods(spuItem){
                if(spuItem.storeNumber==0){
                    this.toastPopMsg('该商品正在努力补货中哦')
                    return;
                }
                let paramData = {
                    ownerCode:this.$userInfo.OWNER_CODE,
                    spuCode:spuItem.spuCode,
                    skuNo:spuItem.skuNo,
                    shopCode:spuItem.shopCode,
                    activitySpuId:spuItem.activitySpuId,
                    skuCount:1,
                    mark:1,
                }
                addGoodsService(paramData).then(res=>{
                    if (res.resultCode == "0") {
                        Toast({
                            message: '添加购物车成功',
                            duration:1000
                        });
                        this.getSkuTypeNum();
                    }else if (res.resultCode == '20100' && res.data.result == '0') {
                        this.toastPopMsg('您所添加商品超过最大购买量了哦');
                    }
                    else{
                        this.toastPopMsg('服务器繁忙')
                    }
                }).catch(error=>{
                })
            },
            //返回上一页
            goBack(isResult = false){
                if (isResult) {
                    this.voiceIconBtn();
                    if (this.tabIndex != '0') {
                        this.tabIndex = '0';
                    }
                }
                else {
                    this.isShowSpeechDiv=false;
                    this.isNoRecognizeResult = false;
                    this.$router.go(-1);
                }
            },
            //去商品详情页
            goToProduct(goodsItem,goodsIndex = -1){
                // if (goodsIndex >= 0) {
                //     sensors.track('SearchResultClick',{
                //         'key_word':this.searchKey,
                //         'position_number':goodsIndex,
                //         'spu_code':goodsItem.spuCode,
                //     })
                // }

                if(goodsItem.activitySpuId){
                    this.$router.push({
                        name: 'flashSaleGoods',
                        query: {
                            spuCode:goodsItem.spuCode,
                            activitySpuId:goodsItem.activitySpuId,
                            goodsUrl:goodsItem.spuPicUrl
                        }
                    })
                }else{
                    this.$router.push({
                        name: 'goodsDetail',
                        params: {
                            spuCode:goodsItem.spuCode,
                            goodsUrl:goodsItem.spuPicUrl
                        }
                    })
                }
            },
            //去购物车页面
            goToShoppingCart(){
                this.$router.push({
                    name: 'shoppingCart',
                    params: {

                    }
                })
            },
            //记录scrollTop
            recordScrollTop(isTabChange = false) {
                let judgeIndex =  this.tabIndex;
                //tab切换时显示的只能取到上一个页面的dom scrollTop值。]
                //进到别的页面时，能取到的是当前页面的dom scrollTop值
                if (!isTabChange) {
                    judgeIndex = this.tabIndex === '0' ? '1' : '0';
                }
                let goodDom = this.$refs.goodContent;
                let shopDom = this.$refs.shopContent;
                if (goodDom && judgeIndex != '0') {
                    this.goodScrollTop = goodDom.scrollTop;
                }
                else if (shopDom && judgeIndex != '1') {
                    this.shopScrollTop = shopDom.scrollTop;
                }
            },
            //设置scrollTop
            setScrollTop() {
                let dom = this.$refs.goodContent;
                let domL = this.$refs.shopContent;
                let noneDom = null;
                if (dom) {
                    if (dom.parentElement.style.display === 'none') {
                        noneDom = dom;
                        noneDom.parentElement.style.display = 'block';
                    }
                    dom ? (dom.scrollTop = this.goodScrollTop) : "";
                }

                if (domL) {
                    if (domL.parentElement.style.display === 'none') {
                        noneDom = domL;
                        noneDom.parentElement.style.display = 'block';
                    }
                    domL ? (domL.scrollTop = this.shopScrollTop) : "";
                }
                if (noneDom) {
                    noneDom.parentElement.style.display = 'none';
                }
            }
        },
        //表单提交事件
        formSubmit () {
            return false
        },
    }
</script>

